@import 'config';

body {
    background-color: $primary-color;
    display: flex;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    margin: 0;
    padding: 30px;
}

.container {
    display: flex;
    width: 100%;
}

//Nav sidebar
.nav-sidebar {
    background-color: $secondary-color;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
    left: 0;
    overflow: hidden;
    padding: 10px;
    position: fixed;
    top: 0; 
    transition: width 0.3s ease;
    width: 112px;
    z-index: 1000;
    
    .menu {
        align-items: center;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        list-style: none;
        margin: 10px;
        padding: 0;
    
        .top {
            align-items: center;
            display: flex;
            margin-bottom: auto;
            margin-top: -15px;
    
            .logo-icon {
                margin-right: 30px; 
            }

            .full-logo {
                display: none;
                //margin-left: 15px;
            }
        }
        
        .bottom {
            margin-bottom: 0px;
            margin-top: auto;
        }
    
        .menu-items {
            margin-top: -10rem; 
    
            li {
                margin-bottom: 2rem; 
            }
        }
    
        li {
            align-items: center; 
            border-radius: 10px;
            color: white;
            cursor: pointer;
            display: flex;
            margin: 10px 0;
            padding: 10px; 
    
            &.active {
                background-color: $tertiary-color;
            }
    
            &:not(.top):hover {
                background-color: $tertiary-color;
            }
        }
    }
    
    &.expanded {
        width: 222px;

        .menu {
            align-items: flex-start;
            margin-left: 25px;
        }

        .top {
            .logo-icon {
                margin-left: -25px;
                margin-right: 22px;
            
            }

            .full-logo {
                display: block;
                margin-left: -9px;
            }        
            
            .arrow-icon {
                margin-left: 40px;
                transform: rotate(180deg);
            }
        }

        .menu-items {
            li {
                justify-content: flex-start;
                
                .menu-text {
                    display: inline-block;
                    margin-left: -5px;
                    opacity: 1;
                }
            }

     
        }
    }

    .menu-items {
        li {
            align-items: center;
            display: flex;
            justify-content: center;
            position: relative;

            .menu-text {
                color: $primary-color;
                display: none;
                left: 60px;
                opacity: 0;
                position: absolute;
                transition: opacity 0.3s ease;
                white-space: nowrap;
            }
        }
    }

        .bottom {
        align-items: center;
        display: flex;
        justify-content: center;
        position: relative;

        .menu-text {
            color: $primary-color;
            display: none;
            left: 60px;
            opacity: 0;
            position: absolute;
            transition: opacity 0.3s ease;
            white-space: nowrap;
        }
    }

    &.expanded {
        .bottom {
            justify-content: flex-start;

            .menu-text {
                display: inline-block;
                margin-left: -5px;
                opacity: 1;
            }
        }
    }
}

// Overlay after opening the sidebar
.overlay {
    background-color: rgba(0, 0, 0, 0.5); // Semi-transparent black
    height: 100%;
    left: 0;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    transition: opacity 0.3s ease;
    width: 100%;
    z-index: 1000; 

    &.active {
        opacity: 1;
        pointer-events: auto;
    }
}

.header {
    align-items: center;
    color: $secondary-color;
    display: flex;
    justify-content: space-between;
    padding: 20px;

    button {
        @include button-styles($secondary-color, white);

        &.secondary-action {
            border: solid 1px $secondary-color!important;
            margin-left: auto;
            margin-right: 10px;
            @include button-styles($quaternary-color, $secondary-color);
        }

        &.primary-action {
            @include button-styles($secondary-color, white);
        }
    }
}

.search-bar {
    align-items: center;
    background-color: $primary-color;
    display: flex;
    margin-top: 20px;
    padding: 10px;

    .search-icon {
        height: 16px;
        left: 15px;
        width: 16px;
    }

    input {
        background-color: $primary-color;
        border: none;
        border-radius: 80px;
        font-family: $font-family;
        font-size: 14px;
        line-height: 24px; 
        outline: none;
        padding: 10px;
        width: 50%;
    }

    .search-button {
        margin-left: auto;
    }


    button.advanced-filters {
        background-color: #E8F1F9;
        border: none; 
        border-radius: 10px; 
        color: #4F575E;
        cursor: pointer;
        font-family: $font-family;
        font-size: 14px;
        line-height: 24px;
        margin-left: 10px; 
        padding: 10px 20px;
    }
}

.search-hr {
    border: none;
    border-bottom: 1px solid $grey-line-color;
    margin: -10px 0 0 25px;
    width: 50%; 
}

.menu {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    list-style: none;
    margin: 10px;
    padding: 0;

    .top {
        align-items: center;
        display: flex;
        margin-bottom: auto;

        .logo-icon {
            margin-right: 30px; 
        }
    }
    
    .bottom {
        margin-top: auto;
    }

    .menu-items {
        margin-top: -10rem; 

        li {
            margin-bottom: 2rem; 
        }
    }

    li {
        align-items: center; 
        border-radius: 10px;
        color: white;
        cursor: pointer;
        display: flex;
        margin: 10px 0;
        padding: 10px; 

        &.active {
            background-color: $tertiary-color;
        }


        &:not(.top):hover { // Only apply hover effect to the bottom menu items
            background-color: $tertiary-color;
        }
    }
    
}

.table-container {
    margin-top: 20px;
    width: 100%;
}

.employees-table {
    border-collapse: separate;
    border-spacing: 0 10px;
    width: 100%;

    thead th {
        color: $senary-color;
        font-weight: 500;
        padding: 15px;
        text-align: left;
    }

    tbody {
        tr {
            background-color: #ffffff;
            border-radius: 15px;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
            margin-bottom: 10px;
            position: relative;

            &:hover {
                background-color: rgb(233, 236, 239);
            }

            td {
                padding: 15px;

                &:first-child {
                    border-bottom-left-radius: 15px;
                    border-top-left-radius: 15px;
                }

                &:last-child {
                    border-bottom-right-radius: 15px;
                    border-top-right-radius: 15px;
                    width: 50px;
                }
            }
        }

        .accordion-content {
            background-color: #ffffff;
            /*display: none;*/
            position: relative;
            top: -20px; /* Pull it up to hide the gap */
            z-index: -1;

            td {
                border-radius: 0 0 15px 15px !important;
                padding: 20px !important;
            }
        }
            .accordion-details {
                display: flex;
                gap: 280px;
                justify-content: space-between;
                margin-top: 10px;
                padding: 15px;

                div:first-of-type > p:nth-of-type(-n+2) {
                    color: #009DDE;
                }
                
            }

        tr.active {
            td:first-child {
                border-bottom-left-radius: 0 !important;
            }

            td:last-child {
                border-bottom-right-radius: 0 !important;
            }
        }
    }
}

.name-container {
    align-items: center;
    display: flex;
    font-weight: 500;
    gap: 10px;
}

.profile-pic {
    border-radius: 50%;
    height: 40px;
    object-fit: cover;
    width: 40px;
}

.status {
    border-radius: 12px;
    font-size: 14px;
    padding: 5px 10px;
}

.dropdown-icon {
    cursor: pointer;
    height: 20px;
    transition: transform 0.3s ease;
    width: 20px;

    &.active {
        transform: rotate(180deg);
    }
}


.sort-icon {
    margin-left: 5px;
}

.placeholder-image {
    background-color: #e0e0e0;
    border-radius: 50%;
    height: 40px;
    width: 40px;
}

.pagination-container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    padding: 10px 0;
}

.pagination-info {
    color: #666;
    font-size: 14px;
}

.pagination-controls {
    align-items: center;
    display: flex;
    gap: 5px;

    .pagination-button {
        background-color: #ffffff;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        padding: 5px 10px;
        transition: background-color 0.3s;

        &:hover {
            background-color: #ddd;
        }
    }
}

/* Name & Profile image alignment */
.name-container {
    align-items: center;
    display: flex;
    gap: 10px;
}

.profile-pic {
    border-radius: 50%;
    height: 35px;
    width: 35px;
}

/* Status badge */
.status {
    background: #EBF7ED;
    border-radius: 15px;
    color: #2A843C;
    display: inline-block;
    font-size: 12px;
    padding: 5px 10px;
    

    &.active {
        background: #2A843C;
        color: #EBF7ED;
    }
}

/* Dropdown icon */
.dropdown-icon {
    cursor: pointer;
    height: 10px;
    margin-left: 10px;
    width: 10px;

    &:hover {
        opacity: 0.7;
    }
}

/* Sidebar toggle button */
.right-sidebar {
    background-color: $quaternary-color;
    box-sizing: border-box;
    height: 100%;
    overflow: auto;
    padding: 20px;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    width: 340px;
    z-index: 999;

    &.expanded {
        transform: translateX(0);
    }

    &::-webkit-scrollbar {
        background: transparent;
        width: 0;
    }

    & > *:nth-child(-n+6) {
        margin-bottom: 40px;
    }
}

.sidebar-toggle-btn {
    align-items: center;
    background-color: #fff;
    border: none;
    border-radius: 15px 0 0 15px;
    color: #fff;
    cursor: pointer;
    display: flex;
    height: 92px;
    justify-content: center;
    padding: 0;
    position: fixed;
    right: 0;
    top: 70px;
    width: 55px;
    z-index: 999;
    transition: right 0.3s ease-in-out; // Move button smoothly

    .arrow-icon {
        height: 27px;
        width: 40px;
        transition: transform 0.3s ease-in-out 0.2s; // Add a slight delay for smooth effect
    }

    &.moved {
        right: 340px; // Moves left when sidebar is expanded
    }

    .arrow-icon.rotated {
        transform: rotate(180deg);
    }
}


// Adjust content margin when sidebar is expanded
.content {
    margin-left: 95px;
    padding: 20px;
    transition: margin-right 0.3s ease;
    width: 100%;

    &.shifted {
        margin-right: 360px; // Adjusts content when sidebar is open
    }
}

.profile {
    align-items: center;
    display: flex;

    p {
        color: #5FA1DA;
    }

    img {
        border-radius: 50%;
        height: 48px;
        margin-left: 10px;
        width: 48px;
    }
}

.profile-info {
    flex-grow: 1;
    margin-left: 15px;
}

.card-data {
    align-items: center;
    background-color: #E8F1F9;
    border-radius: 19px;
    box-sizing: border-box;
    color: #A3C9EA;
    display: flex;
    font-size: 14px;
    height: 76px;
    justify-content: space-around;
    padding: 0 20px;
    width: 308px;

    .card-data-item {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 6px 20px; // Adjust padding to control spacing around the text

        &:not(:last-child) {
            border-right: 1px solid #A3C9EA; // Add a right border to all except the last item
        }


        div {
            text-align: center;

            &:nth-child(2) {
                color:#5FA1DA;
                font-size: 18px; /* Larger font size */
                font-weight: 700; /* Bold font weight */
                margin-top: 4px; /* Add spacing between label and value */

            }
        }
    }
}

.card {
    background-color: #4263EB; 
    background-image: url('/HTML and SCSS Challenge/src/img/medal-card.svg'); // Replace with your image path
    background-position: right; // Position the image at the bottom right
    background-repeat: no-repeat; // Prevent the image from repeating
    background-size: 175px; // Adjust size of the image
    border-radius: 16px;
    overflow: hidden; // Ensure the arrow stays within the card's rounded corners
    position: relative; // Required for absolute positioning of the arrow
    width: 100%; /* Adjust width to fit container */
  
    .card-content {
      display: flex;
      flex-direction: column;
      gap: 12px;
  
      .match-info {

        padding: 20px;

        h3 {
        color: $quaternary-color;
        }

  
        p {
        color: $quaternary-color;
          
        }
      }
  
      .divider {
        background-color: rgba(255, 255, 255, 0.3);
        height: 1px;
        margin: 8px 0 14px;
        width: 75%;
      }
    }

    &::after {
        align-items: center;
        background-color: $quaternary-color; // Arrow background color (matches card background)
        border-bottom-right-radius: 15px;
        border-top-left-radius: 15px;
        bottom: 0px; // Adjust distance from the bottom
        color: #2E45A4; // Arrow color (matches card background)
        content: url('/HTML and SCSS Challenge/src/img/icons/chevron-right.svg'); // Arrow symbol
        display: flex;
        font-size: 0px; // Size of the arrow
        height: 40px; // Size of the rounded background
        justify-content: center;
        position: absolute;
        right: 0px; // Adjust distance from the right
        width: 47px; // Size of the rounded background
      }
  }

